@use 'hero';
@use 'components/buttons';
@use 'variables';
@use 'info';

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Roboto, sans-serif;
    color: variables.$white;
    list-style: none;
}

.container {
    max-width: 960px;
    width: 100%;
    margin: 0 auto;

    @media (max-width: variables.$mobile) {
        max-width: 80%;
    }

    @media (max-width: variables.$desktop) {
        max-width: 90%;
    }
}